@keyframes swipe-left {
  0% {
    opacity: 0.5;
    transform: translateX(24px);
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes swipe-right {
  0% {
    opacity: 0.5;
    transform: translateX(-24px);
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.transition--left {
  -webkit-animation: swipe-left 200ms ease-out;
  animation: swipe-left 200ms ease-out;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.transition--right {
  -webkit-animation: swipe-right 200ms ease-out;
  animation: swipe-right 200ms ease-out;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.resize-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 903;
  user-select: none;
}

.hide-resize-overlay {
  display: none;
}

.header-overlay--active {
  display: block;
}

.main {
  width: 100%;
  height: calc(100% - 64px);
  overflow: hidden;
  display: flex;
  background: var(--black1);
}

/* 1.02 */
.container__calendars {
  height: 100%;
  width: 100%;
  overflow-x: auto;
  margin-left: auto;
}

.container__calendars::-webkit-scrollbar {
  display: none;
}

.cc-tran {
  transition: .3s cubic-bezier(.4, 0, .2, 1);
  transition-property: width, transform;
}

.container__calendars-sb-active {
  width: calc(100% - 256px);
}

.hide-view {
  display: none !important;
}

.yearview,
.dayview,
.monthview {
  width: 100%;
  height: 100%;
  user-select: none;

}

.listview {
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

.listview::-webkit-scrollbar-thumb {
  min-height: 80px;
}

.monthview {
  overflow: hidden !important;
}

.weekview {
  height: 100%;
  width: 100%;
  min-width: 580px;
  overflow-y: hidden;
}

@media screen and (max-width: 840px) {
  .container__calendars {
    width: 100% !important;
  }
}

@media screen and (max-width: 580px) {
  .container__calendars::-webkit-scrollbar {
    display:inherit;
  }
}

@media screen and (max-width: 530px) {
  .main {
    height: calc(100% - 54px);
  }
}

@media screen and (max-height:470px) {
  .main {
    height: calc(100% - 54px);
  }
}